<template>
  <div>
    <h2>导航</h2>
    <ul class="ui-nav">
      <li><router-link :to="{ path: 'index' }">导航页</router-link></li>
      <li><router-link :to="{ path: 'login' }">登陆页</router-link></li>
      <li><router-link :to="{ path: 'collection/index' }">项目</router-link></li>
      <li><router-link :to="{ path: 'dashboard/editor' }">Dashboard</router-link></li>
    </ul>
    <div>
      <el-button>默认按钮</el-button>
    </div>
    <div>
      <el-date-picker
        v-model="value3"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
    </div>
    <div>
      <el-switch
        v-model="value2"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </div>
    <div>
      <el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search"
        v-model="input21">
      </el-input>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6"><el-input
        placeholder="请输入内容"
        prefix-icon="el-icon-search"
        v-model="input21">
      </el-input></el-col>
      </el-row>
    </div>
    <div>
      <el-button :plain="true" @click="open">消息</el-button>      
      <el-button :plain="true" @click="open2">成功</el-button>
      <el-button :plain="true" @click="open3">警告</el-button>
      <el-button :plain="true" @click="open4">错误</el-button>
    </div>
  </div>
  
</template>
<script>
import Http from '../api/index.js'

export default {
  data () {
    return ({
      input21: '',
      value2: false,
      value3: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 20, 10, 10)]
    })
  },
  created () {
    Http.get('collection/item', null, response => {
      console.log(response)
    })
  },
  methods: {
    open () {
      this.$message('这是一条消息提示')
    },
    open2 () {
      this.$message({
        message: '恭喜你，这是一条成功消息',
        type: 'success'
      })
    },
    open3 () {
      this.$message({
        message: '警告哦，这是一条警告消息',
        type: 'warning'
      })
    },
    open4 () {
      this.$message.error('错了哦，这是一条错误消息')
    }
  }
}
</script>
<style>
  .ui-nav li {
    line-height: 2;
  }
</style>



